@font-face {
  font-family: 'Inter';
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src:
    local('Inter-Regular'),
    url(./fonts/Inter-Regular.woff2) format("woff2"),
    url(./fonts/Inter-Regular.woff) format("woff");
}

@font-face {
  font-family: 'Inter';
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src:
    local('Inter-Medium'),
    url(./fonts/Inter-Medium.woff) format("woff2"),
    url(./fonts/Inter-Medium.woff2) format("woff");
}

@font-face {
  font-family: 'Inter';
  font-weight: 900;
  font-style: normal;
  font-display: swap;
  src:
    local('Inter-Black'),
    url(./fonts/Inter-Black.woff2) format("woff2"),
    url(./fonts/Inter-Black.woff) format("woff");
}
